}
#root-todo section .todo-main .all label.chkbox .toggle-all {
          position: absolute;
          display: block;
          width: 30px;
          height: 24px;
          -webkit-border-radius: 3px;
                  border-radius: 3px;
          overflow: hidden;
          left: 3px;
          top: 3px;
          -webkit-box-shadow: 0 2px 5px 1px rgba(0, 0, 0, .2), 0 0 1px white inset;
                  box-shadow: 0 2px 5px 1px rgba(0, 0, 0, .2), 0 0 1px white inset;
          -webkit-transition: -webkit-transform 0.3s ease;
          transition: -webkit-transform 0.3s ease;
          transition: transform 0.3s ease;
          transition: transform 0.3s ease, -webkit-transform 0.3s ease;
          background-image: -webkit-gradient(linear, left top, left bottom, from(white), to(#e7e7e7));
          background-image: -webkit-linear-gradient(white 0%, #e7e7e7 100%);
          background-image: linear-gradient(white 0%, #e7e7e7 100%);
}
