
@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Handlee);
body {
  margin: 0;
}
body ul {
    list-style: none;
}
@-webkit-keyframes textOpacity {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
@keyframes textOpacity {
from {
    opacity: 0;
}
to {
    opacity: 1;
}
}
.progress-bar {
  margin: 10px 20px;
  width: 200px;
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  text-align: center;
  line-height: 200px;
  font-size: 2em;
  font-weight: bold;
  font-family: "Times New Roman", Georgia, Serif;
  -webkit-animation: textOpacity 1.2s linear;
          animation: textOpacity 1.2s linear;
  -webkit-border-radius: 50%;
          border-radius: 50%;
  -webkit-box-shadow: 0 0 20px 5px rgba(0, 0, 10, .5);
          box-shadow: 0 0 20px 5px rgba(0, 0, 10, .5);
}
.progress-bar span {
    text-shadow: 0 0 1px #000;
}
.progress-bar #progress {
    position: absolute;
    left: 0;
    width: 200px;
    height: 200px;
}
.progress-bar #progress circle {
      fill: transparent;
      stroke-dasharray: 565.48;
      stroke-dashoffset: 0;
      -webkit-transition: stroke-dashoffset 1.2s linear;
      transition: stroke-dashoffset 1.2s linear;
      stroke: rgba(0, 0, 10, .3);
      stroke-width: 20px;
}



